import axios, { type AxiosResponse } from 'axios'
import { useSnackbar } from 'notistack'
import { useState } from 'react'
import { Helmet } from 'react-helmet-async'
import { useNavigate } from 'react-router'

import {
  Box,
  Button,
  Checkbox,
  Link,
  TextField as MuiTextField,
  Paper,
  type TextFieldProps,
  Typography,
  useMediaQuery
} from '@mui/material'
import { styled, useTheme } from '@mui/material/styles'

import ConfirmDialog from 'src/components/ConfirmDialog'

import { PrivacyPolicy } from 'src/assets/PrivacyPolicy'

const PageWrapper = styled(Box)(
  () => `
      display: flex;
      justify-content: center;
      align-items: center;
  `
)

const TextField = (props: TextFieldProps) => <MuiTextField size="small" required fullWidth {...props} />

interface RegisterAPIResponse extends AxiosResponse {
  id: number
  studentId: string
  email: string
  nickname: string
}

function Register() {
  const MAX_NICKNAME_LENGTH = 50
  const navigate = useNavigate()
  const theme = useTheme()
  const matches = useMediaQuery(theme.breakpoints.up('sm'))
  const { enqueueSnackbar } = useSnackbar()
  const [checked, setChecked] = useState<boolean>(false)

  const [values, setValues] = useState({
    email: null,
    password: null,
    nickname: null,
    studentId: null
  })

  const [confirmMode, setConfirmMode] = useState<boolean>(false)
  const confirmMessage = `입력하신 학번은 ${values.studentId}입니다. 가입 이후엔 수정하실 수 없습니다.`

  const handleChange: React.ChangeEventHandler<HTMLInputElement> = event => {
    if (event.target.type === 'password') {
      const password = document.getElementById('password') as HTMLInputElement
      const confirmPassword = document.getElementById('confirm-password') as HTMLInputElement
      if (password.value !== confirmPassword.value) confirmPassword.setCustomValidity("Passwords don't match")
      else confirmPassword.setCustomValidity('')
    }
    if (event.target.id !== 'confirm-password') {
      const newValues = {
        ...values,
        [event.target.name]: event.target.value
      }
      setValues(newValues)
    }
    if (event.target && event.target.id === 'nickname') {
      const nickname = event.target
      const trimmedNickName = nickname.value.trim()
      if (trimmedNickName !== nickname.value) {
        nickname.setCustomValidity('닉네임 앞뒤에 공백문자를 포함할 수 없습니다.')
        return
      }
      nickname.setCustomValidity('')
    }
  }

  const dialogOpen: React.FormEventHandler<HTMLFormElement> = e => {
    e.preventDefault()

    if (!checked) {
      enqueueSnackbar('개인정보 처리방침에 동의하셔야 서비스를 이용하실 수 있습니다.', {
        variant: 'error'
      })
      return
    }

    setConfirmMode(true)
  }

  const handleSubmit: React.MouseEventHandler<HTMLButtonElement> = e => {
    e.preventDefault()

    axios
      .post<RegisterAPIResponse>(`${process.env.REACT_APP_APIHOST}/auth/register`, values)
      .then(() => {
        enqueueSnackbar('KLUB의 일원이 되신 것을 환영해요! 이메일 인증 후 서비스 이용이 가능합니다 :)', {
          variant: 'success'
        })
        navigate('/auth/login')
      })
      .catch(err =>
        enqueueSnackbar(err.response.data?.message ?? '서버에서 오류가 발생한 것 같아요. 다시 시도해주세요', {
          variant: 'error'
        })
      )
  }

  return (
    <PageWrapper>
      <Helmet>
        <title>회원가입</title>
      </Helmet>
      <Paper
        square={matches ? false : true}
        sx={{
          my: theme.spacing(8),
          padding: 3,
          flex: 1,
          maxWidth: '480px',
          [theme.breakpoints.down('sm')]: {
            my: 0,
            py: 5,
            maxWidth: '100%'
          }
        }}
      >
        <Typography component="h1" variant="h3" sx={{ mb: 2, textAlign: 'center' }}>
          KLUB에 회원가입
        </Typography>
        <Box
          component="form"
          sx={{
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
            '& .MuiTextField-root': { margin: 1 }
          }}
          onSubmit={dialogOpen}
        >
          <TextField
            id="email"
            name="email"
            type="email"
            label="이메일"
            helperText="인증 메일을 받으실 수 있는 학교 이메일을 입력하세요."
            onChange={handleChange}
          />
          <TextField id="password" name="password" type="password" label="비밀번호" onChange={handleChange} />
          <TextField id="confirm-password" type="password" label="비밀번호 확인" onChange={handleChange} />
          <TextField
            id="student-id"
            name="studentId"
            type="text"
            label="학번"
            helperText="학교에서 부여받은 학번을 입력하세요."
            inputProps={{ pattern: '[0-9]{4}(KU|[0-9]{2})[0-9]{4}', maxLength: 15 }}
            onChange={handleChange}
          />
          <TextField
            id="nickname"
            name="nickname"
            type="text"
            label="닉네임"
            helperText={`${String(values.nickname).length} / ${MAX_NICKNAME_LENGTH}`}
            inputProps={{ maxLength: 50 }}
            onChange={handleChange}
            sx={{
              '& .MuiFormHelperText-root': { textAlign: 'right' }
            }}
          />
          <Typography mt={1} fontWeight="bold">
            개인정보 처리방침
          </Typography>
          <Box sx={{ height: '20vh', overflowY: 'scroll', marginY: 2, backgroundColor: '#eeeeee', p: 1 }}>
            <PrivacyPolicy />
          </Box>
          <Typography>
            위 항목에 동의하십니까?
            <Checkbox defaultChecked={false} onChange={e => setChecked(e.target.checked)} />
          </Typography>

          <Button type="submit" variant="contained" fullWidth sx={{ mt: 1 }}>
            회원가입
          </Button>
          <Link href="/auth/login" sx={{ mt: 1 }}>
            이미 계정이 있으신가요?
          </Link>
        </Box>
        <ConfirmDialog
          open={confirmMode}
          handleClose={() => setConfirmMode(false)}
          handleSubmit={handleSubmit}
          confirmMessage={confirmMessage}
        />
      </Paper>
    </PageWrapper>
  )
}

export default Register
